
.country button {
    margin: 1.5rem 0 4rem;
    padding: .4rem 1.5rem;
    border: none;
    border-radius: 2px;
    box-shadow: 0px 0px 7px rgba(27, 27, 27, 0.452);
    cursor: pointer;
}

.country img {
    object-fit: cover;
    height: fit-content;
    width: 100%;
    max-width: 450px;
}

.country h2 {
    font-size: 1.4rem;
    font-weight: 800;
    margin: 2rem 0 -1rem;
}

.country section {
    margin: 2.5rem 0;
}

.country section p {
    font-size: .85rem;
    font-weight: 600;
    margin: .8rem 0;
}

.country section span {
    font-weight: 300;
}

.borderTag {
    font-size: .7rem;
    margin-right: .6rem;
    padding: .3rem 1rem;
    border-radius: 2px;
    box-shadow: 0px 0px 7px rgba(27, 27, 27, 0.452);
}

@media all and (min-width: 900px) {
    .country .flexBox__outer {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .country .flexBox__outer--left {
        margin-right: 2rem;
    }

    .country img {
        width: 450px;
    }

    .country .flexBox__inner {
        display: flex;
        justify-content: space-between;
    }

    .country .flexBox__inner--left {
        margin-right: 1rem;
    }
}
